	data() {
		return {
			tabList: [
				{
					title: '已预约',
					value: '12'
				},
				{
					title: '使用中',
					value: '12'
				},
				{
					title: '已归还',
					value: '12'
				}
			],
			activeIndex: 0,
			height: 0
		};
	},
	created() {
		uni.getSystemInfo({
			success: res => {
				this.height = res.windowHeight;
			}
		});
	},
	methods: {
		tologi() {
			uni.navigateTo({
				url: '/pages/logistics/logistics',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		changeCurrent(e){
			console.log(e)
			this.activeIndex=e.detail.current
		},
		lookRes(){
			uni.navigateTo({
				url: '/pages/ordel/ordelRes/ordelRes',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		getIndex(index) {
			this.activeIndex = index;
		}
	}
};
